<template>
    <main class="app-main">
        <section class="top">
            <p class="title">科技库</p>
        </section>
        <section class="middle">
            <div class="menu">
                <ul>
                    <li @click="$router.push('/technology/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_n_02@2x.png" alt="">
                        </div>
                        <span>需求</span>
                    </li>
                    <li @click="$router.push('/scienceResult/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_n_03@2x.png" alt="">
                        </div>
                        <span>成果</span>
                    </li>
                    <li @click="$router.push('/device/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_n_04@2x.png" alt="">
                        </div>
                        <span>设备</span>
                    </li>
                    <li @click="$router.push('/expert/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_n_07@2x.png" alt="">
                        </div>
                        <span>专家</span>
                    </li>
                    <li @click="$router.push('/unit/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_n_05@2x.png" alt="">
                        </div>
                        <span>单位</span>
                    </li>
                    <li @click="$router.push('/project/index')">
                        <div>
                            <img src="@/assets/scienceBank/Science_icon_s_06@2x.png" alt="">
                        </div>
                        <span>项目</span>
                    </li>
                </ul>
            </div>
        </section>
        <section class="bottom">
            <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                <md-scroll-view @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <label>{{ i.genre }}</label>
                        <img :src="i.img" alt="">
                        <div class="article">
                            <p v-html="i.name"></p>
                            <span v-text="i.unitName"><img src="@/assets/home/icon_time@2x.png" alt=""></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/technology'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Toast.name]: Toast,
    [ScrollViewMore.name]: ScrollViewMore
  },
  data () {
    return {
      // tab项
      list: [],
      isFinished: false,
      Height: '900px',
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        genre: '',
        province: '',
        city: '',
        area: ''
      },
      show: false
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight - 250 + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.$refs.swiper.goto(index)
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/technology/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        margin-top 88px
        padding-bottom 160px
        .middle
            height 320px
            position fixed
            width 100%
            top 88px
            z-index 9
            .menu
                width 100%
                height 320px
                margin-bottom 50px
                box-shadow 1px 2px #eeeeee
                border-bottom 10px solid rgba(238,238,238,1)
                background-color #ffffff
                display flex
                align-items center
                ul
                    width 100%
                    display flex
                    flex-wrap wrap
                    li
                        flex 0 0 33.3%
                        display flex
                        align-items center
                        justify-content flex-end
                        height 120px
                        flex-direction column
                        div
                            flex 1
                            display flex
                            justify-items center
                            align-items center
                            img
                                width 42px
                        span
                            font-size 24px
                            margin-bottom 20px
        .bottom
            width 690px
            padding-top 348px
            margin 50px auto 0
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 180px
            border-radius 8px
            border 2px solid rgba(2,25,85,0.11)
            background rgba(249,251,255,1)
            label
                position absolute
                color #ffffff
                background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1))
                width 100px
                height 32px
                line-height 32px
                left 8px
                font-size 20px
                font-family PingFang-SC-Regular
                border-radius 0 0 4px 4px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 390px
                text-align left
                margin 25px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
</style>
